/* --- Mixins --- */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  border-top: 5px solid #6db2e2;
  font-family: 'Circe', Arial, sans-serif;
  color: #000000;
}

body header {
  max-width: 1170px;
  padding-left: 45px;
  padding-right: 45px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding-top: 55px;
  justify-content: space-between;
  flex-wrap: wrap;
}

body header .nav-content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

body header .nav-content .phone-contacts {
  margin-right: 60px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

body header .nav-content .phone-contacts .call-me-button {
  background-color: #6db2e2;
  padding: 15px 30px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  border-radius: 25px;
  font-size: 12px;
  margin: 0 40px;
}

body header .nav-content .phone-contacts .call-me-button:hover {
  background-color: #379be0;
}

body header .nav-content .phone-contacts .phone-number {
  font-size: 20px;
}

body header .nav-content .social-icons .social-icon {
  padding: 10px;
}

#repairing {
  max-width: 1170px;
  padding-left: 45px;
  padding-right: 45px;
  margin: 0 auto;
}

#repairing nav {
  text-align: center;
  margin: 55px 0;
  font-weight: bold;
}

#repairing nav a {
  text-decoration: none;
  color: #000000;
}

#repairing nav span {
  color: #6db2e2;
  font-weight: bold;
  font-size: 16px;
  padding: 0 15px;
}

#repairing h2 {
  font-weight: bold;
  font-size: 70px;
  text-align: center;
  line-height: 65px;
  margin-bottom: 90px;
}

#repairing span {
  font-size: 50px;
}

#repairing .repair-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#repairing .repair-items .item {
  padding: 0 30px 90px 30px;
  flex-basis: 25%;
}

#repairing .repair-items .item:hover p {
  color: #6db2e2;
}

#repairing .repair-items .item p {
  font-size: 20px;
  text-align: center;
  margin-top: 35px;
}

@media screen and (max-width: 1060px) {
  body header {
    padding-left: 25px;
    padding-right: 25px;
  }

  body header {
    flex-direction: column;
    justify-content: center;
  }

  body header .logo {
    margin-bottom: 30px;
  }

  #repairing {
    padding-left: 25px;
    padding-right: 25px;
  }

  #repairing h2 {
    font-size: 50px;
  }

  #repairing span {
    font-size: 30px;
  }
}

@media screen and (max-width: 840px) {
  body header {
    padding-left: 15px;
    padding-right: 15px;
  }

  body header .nav-content .phone-contacts {
    margin: 0 auto;
  }

  body header .nav-content .social-icons {
    margin: 30px auto;
  }

  #repairing {
    padding-left: 15px;
    padding-right: 15px;
  }

  #repairing .repair-items .item {
    flex-basis: 40%;
  }
}

@media screen and (max-width: 595px) {
  body header {
    padding-left: 10px;
    padding-right: 10px;
  }

  body header .nav-content .phone-contacts {
    flex-direction: column;
    justify-content: center;
  }

  body header .nav-content .phone-contacts .call-me-button {
    margin: 5px 0;
    order: 1;
  }

  #repairing {
    padding-left: 10px;
    padding-right: 10px;
  }

  #repairing .repair-items .item {
    flex-basis: 80%;
  }
}

@media screen and (max-width: 380px) {
  body header {
    padding-left: 5px;
    padding-right: 5px;
  }

  #repairing {
    padding-left: 5px;
    padding-right: 5px;
  }
}